import React, { Component } from 'react';
import { Button, Input, message } from 'antd';
import 'antd/dist/antd.css';
import "./App.css"
class App extends Component{
  constructor(){
    super();
    this.state = {
      s1: 0,
      s2: 0,
      s3: 0,
      type: ""
    }
  }
  mudar = () => {
    var s1 = this.state.s1;
    var s2 = this.state.s2;
    var s3 = this.state.s3;
    if(s1 == 0 || s2 == 0 || s3 == 0 || s1 < 0 || s2 < 0 || s3 < 0){
      message
        .loading('Action in progress..', 2)
        .then(() => message.error('Please enter valid values', 2.5))
        .then(() => message.info('Please try again', 2.5));
    }else if(s1 > (Math.abs(s2 - s3)) || s1 < (s2 + s3)){
      message
        .loading('Action in progress..', 2.5)
        .then(() => {
          if(s1 === s2 && s1 === s3 && s2 === s3){
            this.setState({
              type: "Equiláterio"
            });
          }else if(s1 !== s2 && s1 !== s3 && s2 !== s3){
            this.setState({
              type: "Escaleno"
            });
          }else if(
            (s1 === s2 && s1 !== s3)
                     ||
            (s1 === s3 && s1 !== s2)
                     ||
            (s2 === s3 && s2 !== s1)
                     ||
            (s2 === s1 && s1 !== s2)
                     ||
            (s3 === s2 && s3 !== s1)
                     ||
            (s3 === s1 && s3 !== s2)
          ){
            this.setState({
              type: "Isocelés"
            });
          }
          
        })
        .then(() => message.success(`O triângulo é ${this.state.type}`, 2.5))
    }else{
      message
        .loading('Action in progress..', 2)
        .then(() => message.error('The existence of this triangle is impossible', 2.5))
        .then(() => message.info('Please try again', 2.5));
    }
  }
  handleS1 = (e) => {
    this.setState({
      s1: e.target.value,
    });
  }
  handleS2 = (e) => {
    this.setState({
      s2: e.target.value,
    });
  }
  handleS3 = (e) => {
    this.setState({
      s3: e.target.value,
    });
  }
  render(){
    return(
      <section>
        <header>
            <h2>Get the type of the triangle in relation to the sides</h2>
        </header>
        <div className="inSides">
          
          <div>
            <label>First Side</label>
            <Input className="gap" min="0" placeholder="The default measurement is cm" onChange={this.handleS1} type="number"></Input>
          </div>
          <div>
            <label>Second Side</label>
            <Input  className="gap" min="0" placeholder="The default measurement is cm" onChange={this.handleS2} type="number"></Input>
          </div>
          <div>
            <label>Third Side</label>
            <Input className="gap" min="0" placeholder="The default measurement is cm" onChange={this.handleS3} type="number"></Input>
          </div>
          <div className="inButton">
            <Button className="gap" placeholder="The default measurement is cm" type="primary"  block ghost onClick={this.mudar}>Calculate</Button>
          </div>
        </div>
      </section>
    );
  }
}

export default App;